body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: url(/static/media/bg.f794583d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.categoryImg{
  width: 50% ;
  height: auto;
}

.buttons{
  width: 100% !important;
}

.icons{
  font-size: 3vw;
  color: white;
}

.counterBasket{
  background: green;
  border-radius: 0.8em;
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  line-height: 1.6em;
  margin-right: 5px;
  text-align: center;
  width: 1.6em;
}

.hoverLink{
  border: 1px solid transparent;

}

.hoverLink:hover{
  border: 1px solid white;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.textDecoration{
  text-decoration: none !important;
}

.subImages{
  width: 20vw !important
}

.imgOrder{
  width: 20% !important;
  height: 20% !important;
}

.sizeP{
  font-size: 2vw !important;
}

.closeButton{
  font-size: 3vw !important;
  margin: 0 !important;
  position: absolute;
  top: -1vw;
  cursor: pointer;
}

.productImg{
  width: 50% !important;
  height: 90% !important;
}


.dankeBox{
  display: none;
}

.infoBox{
  display: block;
}

.divBoxZero{
  display: none;
}

.showBox{
  display: block;
}

.showBoxDanke{
  display: -webkit-flex;
  display: flex;
}

